<template>
    <yd-layout>
        <div v-show="flag==false">
            <div class="setTop">
                <yd-flexbox>
                    <yd-flexbox-item>
                        <router-link to="/remindSet"><span></span>提醒设置</router-link>
                    </yd-flexbox-item>
                    <yd-flexbox-item class="active"><span></span>报表制作</yd-flexbox-item>
                    <yd-flexbox-item>
                        <router-link to="/appCenter"><span></span>应用中心</router-link>
                    </yd-flexbox-item>
                </yd-flexbox>
            </div>
            <div class="remindBtn">
                <div class="remindList">
                    <div class="remindLeft">人事报表</div>
                    <div class="remindRight">
                        <yd-switch v-model="switch1" color="#59a5fd"></yd-switch>
                    </div>
                </div>
                <div class="remindList variousData">
                    <div class="remindLeft">{{leaveData}}</div>
                    <div class="remindRight timeRight" @click="flag=true;dataType=leaveData;checkText=''">{{leaveText}}</div>
                </div>
                <div class="remindList variousData">
                    <div class="remindLeft">{{tripData}}</div>
                    <div class="remindRight timeRight" @click="flag=true;dataType=tripData;checkText=''">{{tripText}}</div>
                </div>
                <div class="remindList variousData">
                    <div class="remindLeft">{{lateData}}</div>
                    <div class="remindRight timeRight" @click="flag=true;dataType=lateData;checkText=''">{{lateText}}</div>
                </div>
                <div class="remindList variousData">
                    <div class="remindLeft">{{earlyData}}</div>
                    <div class="remindRight timeRight" @click="flag=true;dataType=earlyData;checkText=''">{{earlyText}}</div>
                </div>
                <div class="remindList variousData">
                    <div class="remindLeft">{{newData}}</div>
                    <div class="remindRight timeRight" @click="flag=true;dataType=newData;checkText=''">{{newText}}</div>
                </div>
                <div class="remindList variousData">
                    <div class="remindLeft">{{departureData}}</div>
                    <div class="remindRight timeRight" @click="flag=true;dataType=departureData;checkText=''">{{departureText}}</div>
                </div>
                <div class="remindList variousData">
                    <div class="remindLeft">{{expiresData}}</div>
                    <div class="remindRight timeRight" @click="flag=true;dataType=expiresData;checkText=''">{{expiresText}}</div>
                </div>
                <div class="remindList variousData">
                    <div class="remindLeft">{{retiredData}}</div>
                    <div class="remindRight timeRight" @click="flag=true;dataType=retiredData;checkText=''">{{retiredText}}</div>
                </div>
            </div>
            <div class="remindBtn">
                <div class="remindList">
                    <div class="remindLeft">经营数据</div>
                    <div class="remindRight">
                        <yd-switch v-model="switch1" color="#59a5fd"></yd-switch>
                    </div>
                </div>
                <div class="remindList variousData">
                    <div class="remindLeft">{{payData}}</div>
                    <div class="remindRight timeRight" @click="flag=true;dataType=payData;checkText=''">{{payText}}</div>
                </div>
                <div class="remindList variousData">
                    <div class="remindLeft">{{NFCData}}</div>
                    <div class="remindRight timeRight" @click="flag=true;dataType=NFCData;checkText=''">{{NFCText}}</div>
                </div>
                <div class="remindList variousData">
                    <div class="remindLeft">{{QRData}}</div>
                    <div class="remindRight timeRight" @click="flag=true;dataType=QRData;checkText=''">{{QRText}}</div>
                </div>
                <div class="remindList variousData">
                    <div class="remindLeft">{{NFC_QRData}}</div>
                    <div class="remindRight timeRight" @click="flag=true;dataType=NFC_QRData;checkText=''">{{NFC_QRText}}</div>
                </div>
            </div>
        </div>
        <div class="remindBtn selectTime" v-show="flag">
            <div class="remindList selectOne">
                <div class="remindLeft">{{dataType}}</div>
                <div class="remindRight backButton" @click="flag=false;selectText()">返回</div>
            </div>
            <div class="remindList" :class="{'active':checkText=='按天显示'}" @click="checkText='按天显示'">
                <div class="remindLeft">按天显示</div>
            </div>
            <div class="remindList" :class="{'active':checkText=='按周显示'}" @click="checkText='按周显示'">
                <div class="remindLeft">按周显示</div>
            </div>
            <div class="remindList" :class="{'active':checkText=='按月显示'}" @click="checkText='按月显示'">
                <div class="remindLeft">按月显示</div>
            </div>
            <div class="remindList" :class="{'active':checkText=='按季度显示'}" @click="checkText='按季度显示'">
                <div class="remindLeft">按季度显示</div>
            </div>
            <div class="remindList" :class="{'active':checkText=='按年度显示'}" @click="checkText='按年度显示'">
                <div class="remindLeft">按年度显示</div>
            </div>
        </div>
    </yd-layout>
</template>
<script>
export default {
    data() {
        return {
            switch1: false,
            flag: false,
            checkText: '',
            dataType: '',
            leaveData: '请假人员数据',
            tripData: '出差领导数据',
            lateData: '迟到员工数据',
            earlyData: '早退员工数据',
            newData: '新进员工数据',
            departureData: '离职员工数据',
            expiresData: '中高级任期到期数据',
            retiredData: '中高级退休数据',
            payData: '云闪付交易数据',
            NFCData: 'NFC交易数据',
            QRData: '二维码交易数据',
            NFC_QRData: 'NFC+双码数据',
            leaveText: '按周显示',
            tripText: '按周显示',
            lateText: '按周显示',
            earlyText: '按周显示',
            newText: '按周显示',
            departureText: '按周显示',
            expiresText: '按周显示',
            retiredText: '按周显示',
            payText: '按周显示',
            NFCText: '按周显示',
            QRText: '按周显示',
            NFC_QRText: '按周显示'
        }
    },
    mounted() {

    },
    methods: {
        selectText() {
            let vm = this;
            if (vm.dataType == vm.leaveData) {
                if (vm.checkText != '') {
                    vm.leaveText = vm.checkText;
                }
            } else if (vm.dataType == vm.tripData) {
                if (vm.checkText != '') {
                    vm.tripText = vm.checkText;
                }
            } else if (vm.dataType == vm.lateData) {
                if (vm.checkText != '') {
                    vm.lateText = vm.checkText;
                }
            } else if (vm.dataType == vm.earlyData) {
                if (vm.checkText != '') {
                    vm.earlyText = vm.checkText;
                }
            } else if (vm.dataType == vm.newData) {
                if (vm.checkText != '') {
                    vm.newText = vm.checkText;
                }
            } else if (vm.dataType == vm.departureData) {
                if (vm.checkText != '') {
                    vm.departureText = vm.checkText;
                }
            } else if (vm.dataType == vm.expiresData) {
                if (vm.checkText != '') {
                    vm.expiresText = vm.checkText;
                }
            } else if (vm.dataType == vm.expiresData) {
                if (vm.checkText != '') {
                    vm.expiresText = vm.checkText;
                }
            } else if (vm.dataType == vm.retiredData) {
                if (vm.checkText != '') {
                    vm.retiredText = vm.checkText;
                }
            } else if (vm.dataType == vm.payData) {
                if (vm.checkText != '') {
                    vm.payText = vm.checkText;
                }
            } else if (vm.dataType == vm.NFCData) {
                if (vm.checkText != '') {
                    vm.NFCText = vm.checkText;
                }
            } else if (vm.dataType == vm.QRData) {
                if (vm.checkText != '') {
                    vm.QRText = vm.checkText;
                }
            } else if (vm.dataType == vm.NFC_QRData) {
                if (vm.checkText != '') {
                    vm.NFC_QRText = vm.checkText;
                }
            }
        }
    }
}
</script>
<style lang="less" scoped>
.setTop {
    margin-top: 0;
    height: 1.1rem;
    background-color: #fff;
    .yd-flexbox {
        line-height: 1.1rem;
        font-size: .34rem;
        color: #999;
        .yd-flexbox-item {
            text-align: center;
            position: relative;
        }
        .active {
            color: #333;
            span {
                display: block;
                height: .06rem;
                width: 1.64rem;
                border-radius: 2px;
                background-color: #59a5fd;
                position: absolute;
                bottom: -0.03rem;
                left: .42rem;
            }
        }
    }
}

.remindBtn {
    font-size: .34rem;
    background-color: #ececec;
    color: #333;
    border-top: .26rem solid #f8f8f8;
    .remindList {
        height: 1.13rem;
        line-height: 1.13rem;
        padding-left: .54rem;
        padding-right: .44rem;
        border-bottom: 1px solid #eaeaea;
        .remindLeft {
            float: left;
        }
        .remindRight {
            float: right;
            margin-top: .3rem;
        }
    }
    .variousData {
        background-color: #fff;
        .remindLeft {
            color: #666;
        }
        .timeRight {
            margin-top: 0;
            color: #999;
            position: relative;
            padding-right: .25rem;
        }
        .timeRight:before {
            content: '';
            height: .4rem;
            width: .4rem;
            position: absolute;
            top: 0.4rem;
            right: -.3rem;
            background: url(../../../static/images/arrow@.png) no-repeat;
        }
    }
}

.selectTime {
    border-top: none;
    background-color: #fff;
    color: #999;
    .selectOne {
        color: #333;
    }
    .remindList {
        position: relative;
    }
    .remindList:first-child {
        border-bottom: none;
        border-bottom: .26rem solid #f8f8f8;
        height: 1.39rem;
    }
    .backButton {
        color: #59a5fd;
        margin-top: 0!important;
    }
    .active {
        color: #333;
    }
    .active:before {
        content: '';
        height: .4rem;
        width: .4rem;
        display: inline-block;
        background: url(../../../static/images/trs_up_03.png) no-repeat;
        background-size: .36rem;
        position: absolute;
        top: .45rem;
        right: 0.44rem;
    }
}

.yd-switch {
    height: .54rem;
    width: 1.22rem;
}

.yd-switch:before {
    background-color: #dfdfdf;
    height: .45rem;
    width: .45rem;
}

.yd-switch:after {
    height: .45rem;
    width: .45rem; // left: .35rem;
    top: .02rem;
}

.yd-switch:checked:after {
    -webkit-transform: translateX(.73rem);
    transform: translateX(.73rem);
}
</style>


